import react from 'react'
import Caract from './CaracterSheet.jsx'
import css from './../style/Sheets.css'

export default () => {
    var readSheets = () => {        
        var localLenght = localStorage.length;
        var array = [];
        for (var i = 0; i < localLenght; ++i){
            if (localStorage.key(i) == 'ficha_'){
                if (localStorage.key(i+1)){
                    ++i;
                } else {
                    break
                }
            }
            var localName = JSON.parse(localStorage.getItem(localStorage.key(i)));
            array[i] = localName.characterInfo.nome_do_personagem;
            
        }
        var jsx = array.map((name)=>{
            return <Caract name={name}/>;
        });
        return (<div className="historyList">{jsx}</div>);
    }

    var addSheets = () => {
        var data = JSON.parse(localStorage.getItem('ficha_'));
        var doIt = (
            !data.characterInfo.nome_do_personagem || 
            data.characterInfo.nome_do_personagem == '??' ||
            data.characterInfo.nome_do_personagem == 'ficha_'
        )

        if (doIt == true){
            return window.location.reload();
        }
        data.sheet_ID = data.characterInfo.nome_do_personagem;
        localStorage.setItem(data.sheet_ID, JSON.stringify(data));
        return window.location.reload();
    }
    return (
        <>
            <div className="Sheets">
                <h2>HISTORICO DE FICHAS</h2>
                {readSheets()}
            </div>
            <button className="addSheet" onClick={addSheets}>Salvar / Adicionar</button>
        </>
    )
}