import React, { useState } from 'react'
import listStyle from '../style/listStyle.css'
import ByeItems from './ByeItems';
import SkillForm from './SkillForm';
import Popup from './Popup';
import saveImg from './../../icons/save.png'

export default () => {
    var data = JSON.parse(localStorage.getItem('ficha_'));
    var skillArray = data.skills || undefined;
    var nonFullName = (name) => {
        name = name.toUpperCase();
        var returnName = '';
        for (let i=0; i<=13; i++){
            if (!name[i]){
                break;
            }
            returnName += name[i];
            if (i == 13){
                returnName += '...';
            }
        }
        return returnName;
    }
    
    var edit = () => {  
        var docValues = [
            document.getElementsByClassName('input-form')[0],
            document.getElementsByClassName('input-form')[1],
            document.getElementsByClassName('textarea-form')[0]
        ]

        var data = JSON.parse(localStorage.getItem('ficha_'));
        for (var i = 0; i < data.skills.length; i++){
            if (data.skills[i].name == docValues[0].placeholder){
                var ind = ['name', 'type', 'desc'];
                var edited = data.skills[i];
                for (var c = 0; c < docValues.length; c++){
                    edited[ind[c]] = docValues[c].value || docValues[c].placeholder;
                }
                data.skills[i] = edited;
                localStorage.setItem('ficha_', JSON.stringify(data));
                return window.location.reload();
            }
        }
    }

    if (skillArray != undefined && data != undefined){
        return (
            <ul className="InvList">
                {
                    skillArray.map((item, index)=>{
                        const [editSkill, setEditSkill] = useState(false);
                        return (
                        <li>
                            <div className="editSkill" onClick={()=>setEditSkill(true)}>
                                <button>Editar</button>
                            </div>
                            <div className="pWrapper">
                                <p>{nonFullName(item.name)}</p>
                            </div>
                            <ByeItems index={index} inv="skills"/>  
                            <div className="skill-desc">
                                <p>{item.desc}</p>
                            </div>
                            <label className="gun-inp">{item.type}</label>
                            <Popup trigger={editSkill} setTrigger={setEditSkill} tam={[380, 550]}>
                                    <button className="save">
                                        <img src={saveImg} onClick={edit}/>
                                    </button>
                                    <h2>EDITAR HABILIDADE</h2>
                                    <SkillForm haveItem={item}/>
                            </Popup>
                        </li>
                        )
                    })
                }
            </ul>
        );
    } return (<></>);
}