import React from 'react'
import { useState } from 'react'
import img from './../../icons/d20.png'
import Popup from './Popup.jsx'
import DoABarrelRoll from './DoABarrelRoll.jsx'
import distcss from './../style/distorcionDBR.css'
import mimPer from './../data/minValues.json'

export default (req) => {
    var data = JSON.parse(localStorage.getItem('ficha_'));
    const [buttonRollDice, setButtonRollDice] = useState(false);

    var defaultValue = () => {
        data = data.expertises[req.alt] || data.characterInfo[req.alt];
        var local = data || mimPer[(req.alt.replaceAll(' ', '_'))] || 0;
        if (req.alt == 'SAN_MAXIMA'){
            data = JSON.parse(localStorage.getItem('ficha_'));
            data = data[req.alt] || 0;
            local = data;
        }
        if (Number(local) < 10){
            return '0'+local;
        } return local;
    }

    const value = defaultValue();

    return (
        <div className="detail roll">
            <p className="roll-title">{req.inf_req}:</p>
            <label>{value}</label>
            <button className="d20" onClick={()=>{setButtonRollDice(true)}}>
                <img src={img}/> 
            </button>

            <Popup trigger={buttonRollDice} setTrigger={setButtonRollDice} tam={[235, 330]}>
                <DoABarrelRoll per={value}
                complex={true} perName={req.alt}/>
            </Popup>
        </div>  
    )
}