import Attributes from "./components/data/Attributes.json";
import Infos from "./components/data/Form.json";
import minValues from "./components/data/minValues.json"

export default () => {
    var data = JSON.parse(localStorage.getItem("ficha_"));

    console.log([data]);
    var newData = {
        expertises: {},
        characterInfo: {},
        secrets: {AiPassword: "", AmPassword: ""},
        gasta_tudo: 0,
        fixed: true
    }

    for (var att of Attributes){
        var rightOne = () => {
            var with_ = data[((att).replaceAll("_", " ")).replaceAll("combate com ", "")]
            var without = data[(att).replaceAll("combate_com_", "")]
            
            if (with_ > without){
                return with_
            }
            return without;
        }

        newData.expertises[att] = rightOne();

        if (att == "usar_bibliotecas" || att == "usar_computadores"){
            newData.expertises[att] = data[att];

        }
        if (att == "lingua_nativa" || att == "outra_lingua"){
            newData.expertises[att] = 100;
        }
        if (att == "combate_com_armas_irreais" || att == "combate_com_sabres_e_bastões"){
            newData.expertises["combate_com_sabres_e_bastões"] = data["armas_irreais"];
            newData.expertises["combate_com_armas_irreais"] = 0;
        }
    }

    for (var info of Infos){
        newData.characterInfo[info] = data[info];
    }
    
    newData.inventory = data.inventory;
    newData.armor = data.armor;
    newData.checkbox = data.checkbox;
    newData.guns = data.guns;
    newData.historico = data.historico;
    newData.power = data.power;
    newData.skills = data.skills;
    newData.power = data.power;
    newData.gasta_tudo = data.gasta_tudo;
    newData.characterInfo.INVENTARIO_MAXIMO = data.INVENTARIO_MAXIMO;
    newData.characterInfo.MA = data.MA;
    newData.characterInfo.SAN = data.SAN;
    newData.characterInfo.VIDA = data.VIDA;
    newData.enchantment = data.enchantment

    localStorage.setItem("ficha_", JSON.stringify(newData));
    localStorage.setItem(newData.characterInfo.nome_do_personagem || "??", JSON.stringify(newData));
}