import react from 'react'
import Popup from './subconponents/Popup.jsx'
import SkillForm from './subconponents/SkillForm.jsx'
import {useState} from 'react'
import saveImg from '../icons/save.png'
import LoadBalls from './../backend/LoadAmiBalls.js'
import SkillItem from './subconponents/SkillItem.jsx'
import css from './style/skills.css'

export default () => {
    const [openSkillForm, setSkillForm] = useState(false);
    return (
        <div id="Skills" className="card-box">
            <h2>HABILIDADES</h2>
            <div className="list">
                <SkillItem />
            </div>
            <button className="popup-btn" onClick={()=>setSkillForm(true)}>+</button>
            <Popup trigger={openSkillForm} setTrigger={setSkillForm} tam={[360, 550]}>
                <button className="save">
                    <img src={saveImg} onClick={()=>LoadBalls('skills')}/>
                </button>
                <h2>ADICIONAR HABILIDADE</h2>
                <SkillForm />
            </Popup>
        </div>
    )
}