import React from 'react'
import ByeItems from './ByeItems';
import {useState} from 'react'
import Popup from './Popup.jsx'
import EnchantmentForm from './EnchantForm.jsx'
import saveImg from './../../icons/save.png'

export default () => {
    var data = JSON.parse(localStorage.getItem('ficha_'));
    
    var nonFullName = (name) => {
        name = name.toUpperCase();
        var returnName = '';
        for (let i=0; i<=13; i++){
            if (!name[i]){
                break;
            }
            returnName += name[i];
            if (i == 13){
                returnName += '...';
            }
        }
        return returnName;
    }

    var edit = () => {  
        var docValues = [
            document.getElementsByClassName('input-form')[0],
            document.getElementsByClassName('input-form')[1],
            document.getElementsByClassName('textarea-form')[0]
        ]

        var data = JSON.parse(localStorage.getItem('ficha_'));
        for (var i = 0; i < data.enchantment.length; i++){
            if (data.enchantment[i].name == docValues[0].placeholder){
                var ind = ['name', 'weapon', 'desc'];
                var edited = data.enchantment[i];
                for (var c = 0; c < docValues.length; c++){
                    edited[ind[c]] = docValues[c].value || docValues[c].placeholder;
                }
                data.enchantment[i] = edited;
                localStorage.setItem('ficha_', JSON.stringify(data));
                return window.location.reload();
            }
        }
    }

    if (data != undefined && data.enchantment != undefined){
        return (
            <ul className="InvList">
                {
                    data.enchantment.map((item,index)=>{
                        const [editEnchantment, setEditEnchantment] = useState(false);
                        return (
                            <li>
                                <div className="editEnchantment"
                                onClick={()=>setEditEnchantment(true)}>
                                    <button>Editar</button>
                                </div>
                                <div className="pWrapper">
                                    <p>{nonFullName(item.name)}</p>
                                </div>
                                <ByeItems index={index} inv="enchantment"/>  
                                <div className="enchant-desc">
                                    <p>{item.desc}</p>
                                </div>
                                <label className="gun-inp">{item.weapon}</label>
                                <Popup trigger={editEnchantment} setTrigger={setEditEnchantment}
                                tam={[380, 550]}>
                                    <button className="save">
                                        <img src={saveImg} onClick={edit}/>
                                    </button>
                                    <h2>EDITAR ENCANTAMENTO</h2>
                                    <EnchantmentForm haveItem={item}/>
                                </Popup>
                            </li>
                        )
                    })
                }
            </ul>
        )
    } return <></>;
}