import React from 'react'
import style from './../style/form.css'
import formdata from './../data/Attributes.json'
import mimPer from './../data/minValues.json'

export default () => {
    var data = JSON.parse(localStorage.getItem('ficha_'));

    var placeholderMaker = (item) => {
        var pos = data.expertises[item];
        var calc = (Number(pos)-Number(mimPer[item]));
        if (calc >= 0){
            return calc;
        }
        return 0;
        
    }
    return formdata.map((item)=> {
        return (
            <div>
                <p>{item.replaceAll('_', ' ')}:</p>
                <label>{mimPer[item]} +</label>
                <input type="number" onWheel={(e)=>e.target.blur()}
                placeholder={placeholderMaker(item)}
                className={`input-form`} name={`${item}-input`}>
                </input>
            </div>
        )
    })
}