import react from 'react'

export default (req) => {
    var removeSheets = (remove) => {
        localStorage.removeItem(remove);
        window.location.reload();
    }

    var loadSheet = () => {
        localStorage.setItem('ficha_', localStorage.getItem(req.name));
        window.location.reload();
    }

    return (
        <div className="saved-sheets">
            <p onClick={()=>loadSheet(req.name)}>{req.name}</p>
            <label onClick={()=>removeSheets(req.name)}>X</label>
        </div>
    )
}