import React from 'react'
import Roll from './Roll.jsx'
import att from './../data/Attributes.json'

export default () => {
    var storage = JSON.parse(localStorage.getItem('ficha_'));

    var jsx = att.map((Exp)=>{
        if (Exp.startsWith('combate_com_')){
            return;
        }
        return <Roll inf_req={(Exp.replaceAll('_', ' '))} 
        local={storage.expertises[(Exp)]} 
        alt={Exp}/>
    });
    return <div>{jsx}</div>
}