import React from 'react' 
import {useState} from 'react'
import listenAll from '../../backend/listenAll.js';
import setMoodLight from '../setMoodLight.js'

export default (req) => {
    var data = JSON.parse(localStorage.getItem('ficha_'));
    if (!data.checkbox){
        data.checkbox = {};
    }

    if (!data.checkbox[req.order]){
        data.checkbox[req.order] = false;
    }

    var run = () => {
        setMoodLight();
        listenAll();
    }
    return (
        <div className="checkbox">
            <input type="checkbox" className={req.order} onClick={run}
            className={req.order} defaultChecked={data.checkbox[req.order]}/>
            <p>{req.status}</p>
        </div>);
}