import Attributes from './../components/data/Attributes.json'
import minValues from './../components/data/minValues.json'

export default () => {
    var attSumValues = (result, attName) => {
        var attMinValue = minValues[attName];
        if (Number(attMinValue)+Number(result) > 100){
            return 100;
        }
        return Number(attMinValue)+Number(result);
    }

    var data = JSON.parse(localStorage.getItem('ficha_'));
    const inputs = document.querySelectorAll('.input-form');

    for (var input of inputs){
        var inputValue = input.value;

        if (inputValue == '' || inputValue == ' '){
            inputValue = input.placeholder;
        }

        if (input.type == "number" && inputValue > 100){ inputValue = 100 }
        
        var inputName = (input.name).replaceAll("-input","");
        data.expertises[inputName]=attSumValues(inputValue,inputName);
    };

    localStorage.setItem(`ficha_`, JSON.stringify(data));
    window.location.reload();
} 