import React from 'react'
import Popup from './Popup.jsx'
import { useState } from 'react'
import surecss from './../style/sure.css'

export default () => {
    var deleter = () => {
        for (var i = 0; i < localStorage.length; i++){
            localStorage.removeItem(localStorage.key(i));
        }
        return window.location.reload();
    }

    const [haveSure, setSure] = useState(false);
    return (
        <div className="clicker">
            <label onClick={()=>{setSure(true)}} >Deletar</label>
            <Popup trigger={haveSure} setTrigger={setSure} tam={[290, 380]}>
                <div className="SureSure">
                    <h2>Você gostaria de resetar os cookies?</h2>
                    <p>Isso vai deletar todas as fichas, incluindo as do historico, para sempre
                        (Muito tempo)
                    </p>
                    <div>
                        <button onClick={()=>deleter()}>Sim</button>
                        <button onClick={()=>setSure(false)}>Não</button>
                    </div>
                </div>
            </Popup>
        </div>
    );
}