import react from 'react'
import { verify } from 'tweetnacl';
import man from './../../icons/silhoute.png'
import armorcss from './../style/armor.css'

export default () => {
    var data = JSON.parse(localStorage.getItem('ficha_'));

    var checkPlaceholder = (pos, struct) => {
        var portuguese = {
            name: 'Nome',
            defense: 'Defesa',
            weight: 'Peso'

        }
        try {
            if (data.armor[pos][struct] != '' && data.armor[pos][struct] != ' '){
                return data.armor[pos][struct];
            } return portuguese[struct];
        }
        catch {
            return portuguese[struct];
        }
    }
    return (
        <div className="show-armor">
            <img src={man}></img>
            <div className="form-armor">
                <div>
                    <p>Capacete</p>
                    <input className="helmet-name" placeholder={checkPlaceholder('helmet', 'name')}/>
                    <input type="number" className="helmet-defense" placeholder={checkPlaceholder('helmet', 'defense')}/>
                    <input type="number" className="helmet-weight" placeholder={checkPlaceholder('helmet', 'weight')}/>
                </div>
                <div>
                    <p>Peitoral</p>
                    <input className="chestplate-name" placeholder={checkPlaceholder('chestplate', 'name')}/>
                    <input type="number" className="chestplate-defense" placeholder={checkPlaceholder('chestplate', 'defense')}/>
                    <input type="number" className="chestplate-weight" placeholder={checkPlaceholder('chestplate', 'weight')}/>
                </div>
                <div>
                    <p>Perneiras</p>
                    <input className="leggings-name" placeholder={checkPlaceholder('leggings', 'name')}/>
                    <input type="number" className="leggings-defense" placeholder={checkPlaceholder('leggings', 'defense')}/>
                    <input type="number" className="leggings-weight" placeholder={checkPlaceholder('leggings', 'weight')}/>
                </div>
            </div>
        </div>
    )
}