import React from 'react'
import style from './../style/form.css'
import img from './../../icons/save.png'
import pListcss from './../style/pList.css'

export default (req) => {
    const item = req.haveItem;
    var placeholders = ['Nome', 'Outro', '']
    if (req.haveItem && req.haveItem != undefined){
        placeholders = [item.name, item.weapon, item.desc];
    }

    var loadItems = () => {
        var placeholder = 'Outro';
        if (req.haveItem && req.haveItem != undefined){
            placeholder = req.haveItem.name;
        }

        var data = JSON.parse(localStorage.getItem('ficha_'));
        var weapons = [];
        weapons.push('Outro');
        if (data.guns){
            data.guns.map(item=>{
                weapons.push(item.name);
            })
        }
        var jsx = weapons.map(item=>{
            return (
                <option selected={placeholder == item}>{item}</option>
            )
        });
        return (<>{jsx}</>);
    }

    return (
        <div className="form">
            <div>
                <label>Nome:</label>
                <input className="input-form" type="text" placeholder={placeholders[0]}></input>
            </div>
            <div>
                <label>Arma:</label>
                <select className="input-form">
                    {loadItems()}
                </select>
            </div>
            <div>
                <label className="dice-title">Descrição:</label>
                <textarea className="textarea-form" type="text" 
                defaultValue={placeholders[2]}>
                </textarea>
            </div>
        </div>
    )
}