import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App.jsx'
import setMoodLight from './components/setMoodLight.js'

if (!localStorage.getItem('ficha_')){
  var defaultD = {
    expertises: {},
    characterInfo: {},
    secrets: {AiPassword: "", AmPassword: ""},
    gasta_tudo: 0,
    fixed: true
  }

  localStorage.setItem('ficha_', JSON.stringify(defaultD));
}

ReactDOM.render(
  <App />,
  document.getElementById('root')
);

setMoodLight();
