import React from 'react'
import Score from './subconponents/Score.jsx'
import ScoreInv from './subconponents/ScoreInv.jsx'
import Popup from './subconponents/Popup.jsx'
import shield from './../icons/shield.png'
import { useState } from 'react'
import Armor from './subconponents/Armor.jsx'
import saveImg from './../icons/save.png'

export default () => {
    const [showArmor, setShowArmor] = useState(false);
    var data = JSON.parse(localStorage.getItem('ficha_'));

    if (!data.gasta_tudo) {
        data['gasta_tudo'] = 0;
        localStorage.setItem('ficha_', JSON.stringify(data));
    }

    var define = () => {
        var data = JSON.parse(localStorage.getItem('ficha_'));

        const astro = {
            calc: ['VIDA_MAXIMA', 'INVENTARIO_MAXIMO', 'SAN_MAXIMA', 'MA_MAXIMA'],
            adc: ['VIDA_ADICIONAL', 'INVENTARIO_ADICIONAL', 'SAN_ADICIONAL', 'MA_ADICIONAL'],
            sun: [
                ((Number(data.characterInfo.constituição) + Number(data.characterInfo.tamanho)) / 5 || 0),
                ((Number(data.characterInfo.força) + Number(data.characterInfo.tamanho)) / 5 || 0),
                (Number(data.characterInfo.poder) || 0),
                (Number(data.characterInfo.constituição) + Number(data.characterInfo.poder) / 3 || 0)
            ]
        }

        var i = 0;

        for (var item of astro.calc) {
            if (data.characterInfo[astro.adc[i]] == '??') {
                data.characterInfo[astro.adc[i]] = 0;
            }

            data.characterInfo[item] = Math.round(Number(astro.sun[i]) + Number(data.characterInfo[astro.adc[i]] || 0));
            ++i;
        }
        localStorage.setItem('ficha_', JSON.stringify(data));
    }
    define();

    var saveArmor = () => {
        var heavyArmorsGuy = (total, old, now) => {
            return (total - old) + now;
        }

        var totalDefense = 0;
        var totalWeight = 0;
        var totalWeightOld = 0;

        var info = {
            'helmet': {
                'name': "", 'defense': 0, 'weight': 0
            },
            'chestplate': {
                'name': "", 'defense': 0, 'weight': 0
            }, 
            'leggings': {
                'name': "", 'defense': 0, 'weight': 0
            }
        };

        var infOld = {
            'helmet': {
                'weight': 0
            },
            'chestplate': {
                'weight': 0
            }, 
            'leggings': {
                'weight': 0
            }
        };
        
        for (var armor in info) {
            var type = String(armor);
            info[type].name = String(document.getElementsByClassName(`${type}-name`)[0].value);
            info[type].defense = Number(document.getElementsByClassName(`${type}-defense`)[0].value);
            info[type].weight = Number(document.getElementsByClassName(`${type}-weight`)[0].value);
            infOld[type].name = String(document.getElementsByClassName(`${type}-name`)[0].placeholder);
            infOld[type].defense = Number(document.getElementsByClassName(`${type}-defense`)[0].placeholder);
            infOld[type].weight = Number(document.getElementsByClassName(`${type}-weight`)[0].placeholder);

            if (info[type].name == '') info[type].name = infOld[type].name;
            if (info[type].defense == 0) info[type].defense = infOld[type].defense || 0;
            if (info[type].weight == 0) info[type].weight = infOld[type].weight || 0;

            totalDefense += info[type].defense;
            totalWeight += info[type].weight;
            totalWeightOld += infOld[type].weight || 0;
        }
        var data = JSON.parse(localStorage.getItem('ficha_'));
        data['armor'] = info;
        data['total_defense'] = totalDefense;
        data['gasta_tudo'] = heavyArmorsGuy(data.gasta_tudo, totalWeightOld, totalWeight);
        localStorage.setItem('ficha_', JSON.stringify(data));
        window.location.reload();
    }
    return (
        <div id="Points" className="card-box">
            <button className="armor">
                <img src={shield} onClick={() => setShowArmor(true)} />
                <p>{data.total_defense}</p>
            </button>
            <div id="Score">
                <Score inf_req="VIDA" />
                <Score inf_req="SAN" />
                <Score inf_req="MA" />
                <ScoreInv inf_req="INV" />
            </div>
            <Popup trigger={showArmor} setTrigger={setShowArmor} tam={[220, 790]}>
                <button className="save">
                    <img src={saveImg} onClick={saveArmor} />
                </button>
                <Armor />
            </Popup>
        </div>
    )
}