import Form from "./../components/data/Form.json"

export default () => {
    var data = JSON.parse(localStorage.getItem(`ficha_`));
    const inputs = document.querySelectorAll('.input-form');

    for (var input of inputs){
        var inputValue = input.value;

        if (inputValue == '' || inputValue == ' '){
            inputValue = input.placeholder;
        }

        if (input.type == "number" && inputValue > 100){ inputValue = 100 }

        var inputName = (input.name).replaceAll("-input","");
        data.characterInfo[inputName]=inputValue;
    }
    localStorage.setItem(`ficha_`, JSON.stringify(data));
    window.location.reload();
} 