import React from 'react'
import img from './../../icons/d20.png'
import css from './../style/distorcionDBR.css'

export default (req) => {
    var random = Math.floor(Math.random() * 100)+1;

    var saveDiceValuesOnLocalStorage = (dice, perValue, result) => {
        var data = JSON.parse(localStorage.getItem('ficha_'));

        if (!data.diceHistory) {data.diceHistory = []}

        const today = new Date(Date.now());
        const structJson = {
            dice: dice,
            minValue: perValue,
            pericia: req.perName,
            result: result,
            date: today.toLocaleTimeString() + ' - ' + today.toLocaleDateString()
        }

        data.diceHistory.push(structJson);
        localStorage.setItem('ficha_', JSON.stringify(data));
    }

    var rollDice = (per, dice, complex) => {
        if (complex == false){ return [false, dice]; }

        var ComplexValues = ['Desastre', 'Fracasso', 'Normal', 'Bom', 'Extremo'];

        if (complex != true){
            random = 0;

            var yesIDo = {
                add: complex.split('+').length > 1,
                remove: complex.split('-').length > 1
            }

            if (yesIDo.add){
                var diceRoll = complex.split('+');
                
                for (var index of diceRoll){
                    var dSplited = index.split('d');
                    if (dSplited.length === 1){
                        random += Number(dSplited[0]);
                        break;
                    }
                    for (var c = 0; c < Number(dSplited[0]); c++){
                        random += Math.floor(Math.random() * Number(dSplited[1]) + 1);
                    }
                }
            } else if (yesIDo.remove){
                var diceRoll = complex.split('-');
                var removed = [0, 0];
                var i = 0;
                for (var index of diceRoll){
                    var dSplited = index.split('d');
                    if (dSplited.length === 1){
                        random -= Number(dSplited[0]);
                        break;
                    }
                    for (var c = 0; c < Number(dSplited[0]); c++){
                        removed[i] += Math.floor(Math.random() * Number(dSplited[1]) + 1);
                    }
                    ++i;
                }
                random = removed[0];
                for (var c = 1; c < removed.length; c++){
                    random -= removed[c];
                }
            } else {
                var dSplited = complex.split('d');
                for (var c = 0; c < Number(dSplited[0]); c++){
                    random += Math.floor(Math.random() * Number(dSplited[1]) + 1);
                }
            }
            if (String(random).length == 1){
                random = '0'+String(random)
            }
            return [complex, random, true];
        }
        else {
            if (String(dice).length == 1){
                dice = '0'+String(dice)
            }
            return [ComplexValues[ 
                (dice <= per) + (dice <= per / 2) + (dice <= per / 5) + (dice < 100)
            ], dice];
        }
    }

    var final = rollDice(req.per, random, req.complex);

    saveDiceValuesOnLocalStorage(final[1], req.per, final[0]);
    return (
        <div className="DoABarrelRoll">
            <img src={img} alt="d20"/>
            <label className="dice-value">{final[1]}</label>
            <p className="per-value" >{final[0]}</p>
        </div>
    )
}
